import React, { Component } from 'react';
import classes from './page9.less';

class Page extends Component {
  render() {
    return (
        <div className={"section " + classes.content}>
          <div className={classes.middle}>
            <div className={classes.subArea}>
              <div className={classes.description}>
                最开始我们提到了生活垃圾清运量与处理量之间的巨大差额，但没有指明“剩下的垃圾去了哪里”，我们想以这个问题作为结束——面对被垃圾包围的城市，每一个公民都有权利去推动垃圾无害化处理。无论是垃圾分类、还是抗议不合理选址，还是推动政府管理透明，努力做到“垃圾不落地”。
                <br />

              </div>
            </div>
          </div>
          <div className={classes.footer}>
            第二届高校数据新闻报道大赛参赛组：郝有峰 唐伊豆 岳汀 姚倩 吴开元 罗晓月
            <br />
            感谢深圳市城市管理局提供支持
          </div>
        </div>
    )
  }
}

export default Page;


