import React, { Component } from 'react';
import classes from './page8.less';

class Page extends Component {
  render() {
    return (
        <div className={"section " + classes.content}>
          <div className={classes.middle}>
            <div className={classes.subArea}>
              <div className={classes.description}>
                虽然已经有了较为清晰和量化的规范，但中国垃圾场选址仍有可优化的空间。
                <br />
                很多发达国家的垃圾焚烧发电厂都建在城市中心，执行严格的环保标准，同时便于充分利用热能。
                <br />
                在我国，由于对垃圾焚烧厂污染控制技术和焚烧厂运营的经验和信心不足，所以对焚烧厂的选址控制相对严格。
                <br />
                但在科技日益发展的今天，我们有理由相信未来中国的垃圾焚烧厂选址将更加科学合理。
              </div>
            </div>
          </div>
        </div>
    )
  }
}

export default Page;


