import React, { Component } from 'react';
import classes from './page3.less';

class Page extends Component {
  render() {
    return (
        <div className={"section " + classes.content}>
          <div className={classes.middle}>
            <div className={classes.subArea}>
              <div className={classes.description}>
                1983年，深圳市投入使用第一个生活垃圾填埋场：玉龙坑垃圾填埋场。
                <br />
                90年代的填埋场，多采用简易填埋，后来陆续改用卫生填埋，但大多存在超量处理的情况，有的已封场填埋场，甚至被私自破坏性重启。
                <br />
                焚烧发电是目前较为安全环保的垃圾处理方式，技术成熟，国际通用。2015年，深圳市的垃圾焚烧处理能力为7425吨/日，焚烧处理率仅50%。
                <br />
                为此，全市计划将新建三座生活垃圾焚烧处理设施，规划期末全市生活垃圾焚烧处理能力将达到17425吨/日，可以保障焚烧处理率达到100%的目标。
              </div>
            </div>
          </div>
        </div>
    )
  }
}

export default Page;


