import garbageQuantity, { garbageKeyMapping } from '../../data/garbage';
import React, { Component } from 'react';
import classes from './page6.less';
import EChart from '../echarts/echarts';
import { protest as protestData, complain as complainData} from '../../data/protest';
import _ from 'lodash';

const chartOption = () => {

  const months = _.range(1, 13).map(y => y + '月');
  const years = _.range(2003, 2016).map(y => y + '年');

  // echarts没有当月数据就会把这个轴隐藏...,强行加入一个数据
  const pData = protestData.map(d => {
    return [d.month - 1, d.year - 2003, d];
  });
  const cData = [..._.range(2003, complainData[0].year).map(d => '-'),
      ...complainData.map(d => d.sum)];
  return {
    legend: {
      data: ['大规模抗议事件', '环境信访投诉量'],
      left: 'center',
      top: '5%'
    },
    tooltip: {
      alwaysShowContent: true,
      enterable: true,
      position: 'bottom',
      formatter: function (params) {
        if (params.seriesName === '大规模抗议事件') {
          const data = params.value[2];
          const title = _.truncate(data.title, {length: 10, omission: '...'});
          return `${data.year}年${data.month}月${data.date}日` +
              `<br />${data.landfill}<br />` +
              `<a target="_blank" class=${classes.labelLink} href="${data.link}">相关链接:${title}</a>`;
        } else if (params.seriesName === '环境信访投诉量') {
          return `${params.name}环境信访投诉量:${params.data}`;
        }
      }
    },
    grid: {
      left: 2,
      bottom: 10,
      right: 10,
      containLabel: true
    },
    xAxis: [{
      type: 'category',
      data: months,
      boundaryGap: false,
      splitLine: {
        show: true,
        lineStyle: {
          color: '#fff',
          type: 'dashed'
        }
      },
      axisLine: {
        show: false
      }
    }, {
      type: 'value',
      name: '环境信访投诉',
      splitLine: {
        show: false
      },
      axisLine: {
        show: false
      }
    }],
    yAxis: {
      type: 'category',
      data: years,
      axisLine: {
        show: false
      },
      splitLine: {
        show: true,
        lineStyle: {
          color: '#fff',
          type: 'dashed'
        }
      }
    },
    series: [{
      name: '大规模抗议事件',
      type: 'scatter',
      symbolSize: 10,
      data: pData
    }, {
      name: '环境信访投诉量',
      type: 'line',
      xAxisIndex: 1,
      data: cData,
      areaStyle: {normal: {}},
      symbolSize: 10
    }]
  };
};


const style = {
  width: '100%',
  height: '80%'
};

class Page extends Component {
  constructor(props) {
    super(props);
    this.state = {
      chartOption: chartOption()
    };
  }
  render() {
    return (
        <div className={"section " + classes.content}>
          <EChart ref='echart' option={this.state.chartOption} style={style} theme="dark" />
          <div className={classes.garbageText}>
            据不完全统计，2003年以来，共有14次垃圾抗议事件被媒体报道，涉及到下坪、玉龙坑、坪山、白鸽湖等地使用中的垃圾处理设施，也有针对规划中的垃圾处理设施选址的抗议。
            <br />
            与此同时,环境信访部门收到的投诉也日益增加。
          </div>
        </div>
    );
  }
}

export default Page;



