import React, { Component } from 'react';
import classes from './page5.less';

class Page extends Component {
  render() {
    return (
        <div className={"section " + classes.content}>
          <div className={classes.middle}>
            <div className={classes.subArea}>
              <div className={classes.description}>
                当垃圾填埋场“吃饱”之后，每天焚烧不完的垃圾要么堆放在露天，要么在填埋场之外找个地方填埋。原来地处偏远的垃圾场，也在城市化扩张中变得离居住区越来越近，臭气、烟尘、污水……垃圾围城的警报，已经拉响。
              </div>
            </div>
          </div>
        </div>
    )
  }
}

export default Page;


