import garbageQuantity, { garbageKeyMapping } from '../../data/garbage';
import React, { Component } from 'react';
import classes from './page2.less';
import EChart from '../echarts/echarts';

const chartOption = () => {
  const seriesAll = {
    data: garbageQuantity.map(data => data['深圳市年垃圾清运量']),
    name: '深圳市年垃圾清运量',
    type: 'line'
  };

  const seriesClean = {
    name: '深圳市年生活垃圾无害化处理量',
    type: 'line',
    areaStyle: {normal: {}},
    data: garbageQuantity.map(data => data['深圳市年生活垃圾无害化处理量'])
  };

  const seriesHelperForMarkLine = {
    data: garbageQuantity.map((item, index) => {
      const half = garbageQuantity.length / 2;
      if (index < half - 1 || index > half) return 0;
      return 533;
    }),
    type: 'line',
    markLine: {
      data: [
        {
          type: 'max',
          label: {
            normal: {show: true, position: 'start'}
          }
        }
      ]
    },
    markPoint: {
      label: {
        normal: {
          formatter: '市环卫专项规划设计中2020年清运量预测值',
          show: true,
          position: 'top'
        }
      },
      data: [
        {
          type: 'max',
          symbol: 'pin',
          name: '533',
          symbolSize: 1
        }
      ]
    },
    itemStyle: {
      normal: {opacity: 0},
      emphasis: {opacity: 0}
    },
    lineStyle: {
      normal: {opacity: 0},
      emphasis: {opacity: 0}
    }
  };

  const series = [seriesClean, seriesAll, seriesHelperForMarkLine];

  const years = garbageQuantity.map(data => data['年份']);
  const option = {
    title: {
      text: '深圳市生活垃圾清运与处理量',
      left: 'center',
      padding: 20
    },
    tooltip : {
      trigger: 'axis',
      formatter: (params) => {
        const all = params[1];
        const clean = params[0];
        let text = `${all.name}年: <br />`;
        text += `${all.seriesName}:`;
        if (all.value) {
          text += `${all.value}万吨<br />`;
        } else {
          text += `未知<br />`;
        }
        text += `${clean.seriesName}: `;
        if (clean.value) {
          text += `${clean.value}万吨<br />`;
        } else {
          text += `未知<br />`;
        }
        if (all.value && clean.value) {
          const percent = (100 * (clean.value / all.value)).toFixed(2);
          text += `无害化比例: ${percent}%`;
        }
        return text;
      }
    },
    grid: {
      left: '3%',
      right: '4%',
      bottom: '3%',
      containLabel: true
    },
    xAxis: [
      {
        boundaryGap : false,
        type: 'category',
        data: years,
        name: '年份',
        min: years[0],
        max: years[years.length - 1]
      }
    ],
    yAxis: [
      {
        type : 'value'
      }
    ],
    series: series
  };
  return option;
};


const style = {
  width: '100%',
  height: '60%'
};

class Page2 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      chartOption: chartOption()
    };
  }
  render() {
    return (
        <div className={"section " + classes.content}>

          <EChart ref='echart' option={this.state.chartOption} style={style} theme="dark" />
    
          <div className={classes.garbageText}>
            1. 本作品讨论居民生活垃圾，不包括市政修建渣土、食品加工业废物、医疗废物等。清运量是参与转运的垃圾量。无害化处理量未包括简易填埋场的垃圾处置量。可以认为，垃圾的实际产生量大于清运量和无害化处理量，难以完全统计。
            <br />
            2. 数据来源：深圳统计年鉴
            <br />
          </div>
          <div className={classes.description}>
            从2014年开始，深圳市生活垃圾清运量就已超过专项规划预测值，而且还在呈现继续扩大的趋势。垃圾无害化处理形势严峻。然而，几十年来深圳市的垃圾焚烧厂推动显得步履维艰。
          </div>
        </div>
    );
  }
}

export default Page2;



