import React, { Component } from 'react';
import classes from './page7.less';

class Page extends Component {
  render() {
    return (
        <div className={"section " + classes.content}>
          <div className={classes.middle}>
            <div className={classes.subArea}>
              <div className={classes.description}>
                目前中国关于垃圾处理设施都有了相应的控制标准和规定。
                <br />
                对于垃圾填埋场，要求其周围应设置绿化带，绿化带宽度不小于10米。
                <br />
                对于垃圾焚烧厂，从选址角度讲，要求设于离湖泊、河流、湿地、洪水淹没区、供水井及机场有一定距离的地方。另外，也不允许设于可能对现有地下水或地表水产生污染的区域中。一般要求与铁路和公路的距离大于300米且小于1500米，也必须远离居民区，距离居民区至少500米且最好位于居民区的下风向。
              </div>
            </div>
          </div>
        </div>
    )
  }
}

export default Page;


