import React, { Component } from 'react';
import classes from './page1.less';
import downArrow from '../../../imgs/down-arrow.svg';

class Page extends Component {
  render() {
    return (
        <div className={"section " + classes.content}>
          <div className={classes.middle}>
            <div className={classes.title}>
              深圳垃圾围城记
            </div>
            <div className={classes.subArea}>
              <div className={classes.subtitle}>
                一次垃圾场的再启蒙
              </div>
              <div className={classes.description}>
                1979年，深圳市生活垃圾清运量为7.5吨/日，截至2015年底， 这一数量达到15748吨/日，增长约2000倍。
              </div>
            </div>
          </div>
          <div className={classes.prompt} style={{opacity: 1}}>
            <p>向下滑动</p>
            <img src={downArrow} />
          </div>
        </div>
    )
  }
}

export default Page;


