import React, { Component } from 'react';
import classes from './landfillCard.less';
import { landfillColorMapping, statusMapping } from '../../utils/constants';


class LandfillCard extends Component {
  render() {
    let { landfillInfo } = this.props;
    const addressInfo = landfillInfo.address ? `位于${landfillInfo.address}。` : '';

    let date = '';
    if (landfillInfo.openDate) date += `投产于${landfillInfo.openDate}年。`;
    if (landfillInfo.closeDate) date += `关闭于${landfillInfo.closeDate}年。`;

    let processed = '';
    if (landfillInfo.area) processed += `占地${landfillInfo.area}平方米。`;
    if (landfillInfo.capacity) processed += `至今总处理量${landfillInfo.capacity}万吨。`;


    let note = landfillInfo.note || '';
    if (note) note += '。';
    return (
        <div className={classes.card}>
          <h2 style={{color: landfillColorMapping(landfillInfo.status)}}>
            {landfillInfo.name}({statusMapping[landfillInfo.status]})
          </h2>
          <p className={classes.description}>
            {landfillInfo.type}。{addressInfo}
            <br />
            {date}
            <br />
            {processed}
            <br />
            {note}
          </p>
        </div>
    )
  }
}

export default LandfillCard;


